<script>
import Buttons from '@/components/buttons/button.vue'

export default {
  name: 'Register',
  components:{
    Buttons,
  },
  data()
  {
    return {
      hasEmpty: false,
      hasSucceeded: false,
      password: '',
      passwordSuccess:'',
      errorMessage:'',
      errorMessageEmail:'',
      hasErrors: false,
      email: '',
      emailSuccess:'',
      errorCount:0,
      terdaftar: false,
      items: [
        {
          caption: 'E-Koran Kompas'
        },
        {
          caption: 'Artikel Terbaru, dan Reportase Langsung'
        },
        {
          caption: 'Rubrik Opini Publik, Populer, Indikator, Pantau Media, dan Telah'
        },
        {
          caption: 'Rubrik Tutur Visual, Suara Kompas, Editor’s Choice, Datagrafik, dan Fotostory'
        },
        {
          caption: 'Semua artikel di Kompas.id'
        },
      ],
      userData:[
        {
          user:'email@email.com',
          pass:'123qwe123'
        },
        {
          user:'email1@email.com',
          pass:'123qwe123'
        },
        {
          user:'email2@email.com',
          pass:'123qwe123'
        },
        {
          user:'email3@email.com',
          pass:'123qwe123'
        },
      ],

    }
  },
  // watch:{
  //   email(value){
  //     var filtered = this.userData.filter(function(ob) {
  //       return ob['user'].toLowerCase().includes(value.toLowerCase());
  //     });
  //     if(filtered != ''){
  //       console.log('ada email sama')
  //     }else{
  //       console.log('tidak')
  //     }
  //   }
  // },
  methods: {
    inputClassesEmail() {
      let classes = [
        'appearance-none',
        'border',
        'border-blue-royal-500',
        'font-pt-sans',
        'leading-tight',
        'mb-4',
        'p-4',
        'rounded-sm',
        'text-base',
        'text-gray-600',
        'w-full',
        'focus:outline-none',
      ]

      if (this.hasErrors) classes.push('border-red-thunderbird-700 bg-red-thunderbird-100')
      return classes.join(' ')
    },
    check(){
      const _self = this
      var filtered = _self.userData.filter(function(ob) {
        return ob['user'].toLowerCase().includes(_self.email.toLowerCase());
      });
      if(filtered != ''){
        return false
      }else{
        return true
      }
    },
    inputClassesPassword() {
      let classes = [
        'appearance-none',
        'border',
        'border-none',
        'font-pt-sans',
        'leading-tight',
        'p-4',
        'rounded-sm',
        'text-base',
        'text-gray-600',
        'w-full',
        'focus:outline-none',
      ]

      if (this.hasEmpty) classes.push('border-red-thunderbird-700 bg-red-thunderbird-100')
      return classes.join(' ')
    },
    isStrongPass(password) {
        var regExp = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%&*()]).{10,}/;
        var validPassword = regExp.test(password);
        return validPassword;
    },
    myFunction() {
      var x = document.getElementById("inputPassword");
      if (x.type === "password") {
          x.type = "text";
      } else {
          x.type = "password";
      }
    },
    submit() {
      const _self = this
      if(this.email ===''){
          this.hasErrors = true
          this.errorMessageEmail = 'Email harus diisi'
      }else{
        this.hasErrors = false
      }
      if (this.password === ''){
        this.hasEmpty = true
        this.errorMessage = 'Kata sandi harus diisi. '
      }else{
        this.hasEmpty = false
      }
      
      if(this.email !='' && this.password != ''){
        console.log(this.isStrongPass(this.password))
        if(this.validateEmail(this.email)){
          if(this.check()) {
              console.log('user belum ada')
              this.hasSucceeded = true
            }else{
                console.log('user udah ada')
                this.terdaftar = true
          }
        }else{
          if(this.validateEmail(this.email) === false){
            this.hasErrors = true
            this.errorMessageEmail = 'Masukkan format email yang benar.'
          }
          if(this.isStrongPass(this.password) === false){
            this.hasErrors = true
            this.errorMessage = 'Kata sandi harus sesuai yang ditentukan.'
          }
        }
      }
    },
    validateEmail(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    }
  }
}

 
</script>