<template lang="pug">
div
  p.capitalize.font-bold.font-chronicle-display.leading-normal.pt-8.text-2xl.text-center.text-gray-600 ganti kata sandi
  //- Text
  p.font-pt-sans.leading-relaxed.max-w-sm.mx-auto.pt-2.pb-4.text-base.text-center.text-gray-600 Silahkan masukkan kata sandi baru Anda.
  //- Box Gray
  div.border.border-gray-100.max-w-sm.mx-auto.my-4.p-6.rounded(v-if='!hasSucceeded')
    //- Input Ganti Kata Sandi
    div.border.border-gray-100.flex.flex-row.mb-4(:class='hasEmpty?"border-red-thunderbird-700 bg-red-thunderbird-100":""')
      input(v-model='password', :class="inputClassesPassword()", type="password"  id="inputPassword" placeholder="Masukkan kata sandi baru" ref='password' )
      button.bg-transparent.flex.flex-row.self-center.items-center.justify-center(@click='myFunction', class='w-1/4')
        font-awesome-icon.mr-0.text-base.text-gray-300(class='lg:mr-2', :icon=['far', 'eye'])
        p.text-gray-450.font-pt-sans.hidden.text-sm(class='lg:inline') Lihat
    div.pb-4(v-if='hasEmpty')
      font-awesome-icon.mr-2.text-base.text-red-thunderbird-700(:icon=['fas', 'exclamation-circle'])
      span.font-pt-sans.text-sm.text-red-thunderbird-700 {{ errorMessage }} 
    p.font-pt-sans.leading-snug.text-base.text-gray-600 Kata sandi wajib mengandung huruf kapital, angka dan simbol (@,#,$,dst.), dengan jumlah minimal 10 karakter. 

    //- Button  
    Buttons.my-4(@submit="submit", titleDrop="masuk" wFull=true bold=true blue=true sizeFont='base')
    //- button(@click='submit') TEST 

  //- Email Has Been Sent
  div.bg-blue-royal-100.border.border-blue-royal-700.flex.flex-row.max-w-sm.mx-auto.my-4.p-6.rounded-sm(v-else)
    div.mr-2.self-center
      font-awesome-icon.mr-4.text-2xl.text-blue-royal-700(:icon=['fas', 'check-circle'])
    div.flex.flex-col
      p.font-bold.font-pt-sans.text-base.text-gray-600 Tautan Telah Dikirim 
      p.font-pt-sans.text-base.text-gray-600 Silahkan gunakan kata sandi tersebut untuk 
        span.font-bold.text-blue-royal-500 masuk 
        span ke Kompas.id.
</template>

<script>
import Buttons from '@/components/buttons/button.vue'

export default {
  name: 'PasswordSecondStep',
  components:{
    Buttons,
  },
  data()
  {
    return {
      hasEmpty: false,
      hasSucceeded: false,
      password: '',
      passwordSuccess:'',
      errorMessage:''
    }
  },
  methods: {
    inputClassesPassword() {
      let classes = [
        'appearance-none',
        'border',
        'border-none',
        'font-pt-sans',
        'leading-tight',
        'p-4',
        'rounded-sm',
        'text-base',
        'text-gray-450',
        'w-full',
        'focus:outline-none',
      ]

      if (this.hasEmpty) classes.push('border-red-thunderbird-700 bg-red-thunderbird-100')
      return classes.join(' ')
    },
    submit() {
      if (this.password === ''){
        this.hasEmpty = true
        this.errorMessage = 'Kata sandi harus diisi. '
      }else{
        if(this.isStrongPass(this.password)){
          this.hasSucceeded = true
        }else{
          this.hasEmpty = true
          this.errorMessage = 'Kata sandi harus sesuai yang ditentukan.'
        }
      }
    },
    isStrongPass(password) {
        var regExp = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%&*()]).{10,}/;
        var validPassword = regExp.test(password);
        return validPassword;
    },
    myFunction() {
      var x = document.getElementById("inputPassword");
      if (x.type === "password") {
          x.type = "text";
      } else {
          x.type = "password";
      }
    }
  }
}
 
</script>